.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 100px;
  margin-top: 50px;
  position: relative;
}

.content {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content ul {
  padding: 0;
}

.bookContainer {
  display: flex;
  height: auto;
  width: 80vw;

  margin-bottom: 20px;
  font-family: 'GmarketSansMedium';
}

.bookImg {
  border-radius: 5%;
  /*margin-right: 30px;*/
  width: 40;

  /*filter: drop-shadow(3px 3px 3px #727272);*/
}

.detail {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  background-color: white;
  filter: drop-shadow(0px 1px 2px #9b9b9b);
  padding: 20px;
  border-radius: 5%;
}

.detail > div span {
  display: block;
}

.title {
  font-weight: 500;
  font-size: 1.5rem;
  margin-right: 4px;
}

.author {
  font-size: 1.2rem;
  font-weight: 500;
  color: rgb(138, 138, 138);
  margin-top: 10px;
}
.detailInfo {
  font-size: 1rem;
  margin-top: 10px;
}

.detailInfo div:last-child {
  margin-top: 10px;
}

.BookReportTitle {
  font-size: 1.2rem;
  margin-top: 10px;
}

.settings {
  width: 80vw;
  margin-top: 60px;
  font-size: 22px;
  font-family: 'GmarketSansMedium';
  /* display: block;*/
}

.setting {
  margin-top: 40px;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.setting span{
  /*text-decoration: underline;
  text-underline-position:under;*/
  padding-bottom: 0px;
}

.hr{
  height: 3px;
  background-color : rgb(0, 0, 0);
  border : 0;
  margin-bottom: 10px;
}

.message{
  margin-top: 8px;
  margin-bottom: 6px;
  font-size: 2vw;
  font-family: "Happiness-Sans-Regular";
  color: #474747;

}